<template>
  <v-sheet height="100%" class="overflow-hidden">
    <v-card light>
      <v-card-text>
        <v-col class="text-center">
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-center" width="100">狀態</th>
                  <th class="text-center">捲門序號</th>
                  <th class="text-center">名稱</th>
                  <th class="text-center">客戶</th>
                  <th class="text-center">Actions</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in doors" :key="item.id">
                  <td>
                    <v-chip
                      class="ma-2"
                      :class="getStatusColor(item.status)"
                      :text-color="getStatusTextColor(item.status)"
                    >
                      {{ getStatusTitle(item.status) }}
                    </v-chip>
                  </td>
                  <td>{{ item.sn }}</td>
                  <td>{{ item.name }}</td>
                  <td>{{ item.client_name }}</td>
                  <td>
                    <v-card-text v-if="item.completed == false">已派單</v-card-text>
                    <v-btn v-else text @click="openCreateAssign(item)">派單</v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-col>
        <div class="pageinfo">
          <v-container class="max-width">
            <v-pagination
              v-model="pagination.currentPage"
              class="my-4"
              :length="pagination.totalPage"
              @input="handlePageChange"
            ></v-pagination>
          </v-container>
        </div>

        <component :is="showView" :initialSelected="selectedDoor"></component>
      </v-card-text>
    </v-card>
  </v-sheet>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { statusConvert } from "../plugins/statusConvert";
import AssignCreate from "./AssignCreate";

export default {
  status: "DoorMapList.",
  created() {
    console.log("Create DoorMapList")
    let city_ids = this.selectAreaCityIds;
    let statuses = [1,2,4,5];
    let currentPage = 1;
    this.getDoorsByCitysAndStatuses({ statuses, city_ids, currentPage });
  },
  components: {
    AssignCreate,
  },
  data() {
    return {
      drawer: null,
      selectedDoor: null,
    };
  },
  methods: {
    ...mapActions("doorsModule", ["getDoorsByCitysAndStatuses"]),
    ...mapActions("assignModule", ["showCreateAssignDailog"]),
    getStatusColor: statusConvert.getStatusColor,
    getStatusTitle: statusConvert.getStatusTitle,
    getStatusTextColor: statusConvert.getStatusTextColor,
    handlePageChange(currentPage) {
      let city_ids = this.selectAreaCityIds;
      let statuses = [1,2,4,5];
      this.getDoorsByCityAndStatus({ statuses, city_ids, currentPage });
    },
    openCreateAssign(door) {
      // console.log(door)
      door.door_sn = door.sn;
      door.door_id = door.id;
      this.selectedDoor = door;
      this.showCreateAssignDailog({ isShow: true, view: AssignCreate });
    },
  },
  computed: {
    ...mapState("doorsStatusModule", ["selectAreaCityIds",]),
    ...mapState("doorsModule", ["doors", "pagination"]),
    ...mapState("assignModule", ["showView"]),
    dailogComponent() {
      return this.dialogTag;
    },
  },
  // watch: {
  //   urgentCitys(show) {
  //     if (show) {
  //       console.log("urgentCitys:", this.urgentCitys);
  //       // let city_ids = this.selectedUrgentCitys;
  //       // let status = 0;
  //       // let currentPage = 1;
  //       // this.getDoorsByCityAndStatus({ status, city_ids, currentPage });
  //     }
  //   },
  // },
};
</script>
