import { userService } from '../service/user.service.js';
import config from '../config'
const TAG = "userModule"
const user = JSON.parse(localStorage.getItem('user'));

const menu = [{
        icon: "mdi-map-outline",
        subitems: [
            { title: "緊急", to: "/mapUrgent" },
            { title: "區域", to: "/mapArea" },
        ],
        title: "即時地圖",
        active: false,
    },
    {
        icon: "mdi-view-list-outline",
        title: "捲門列表",
        subitems: [],
        to: "/doorList",
        active: false,
    },
    {
        icon: "mdi-car",
        title: "車輛列表",
        subitems: [],
        to: "/carList",
        active: false,
    },
    {
        icon: "mdi-post-outline",
        subitems: [
            { title: "派單中", to: "/assignList" },
            { title: "派單紀錄", to: "/assignRecord" },
        ],
        title: "派單",
        active: false,
    },
    {
        icon: "mdi-folder-text",
        subitems: [
            { title: "操作紀錄", to: "/logList", admin: true },
            { title: "捲門紀錄", to: "/logDoorList" },
        ],
        title: "紀錄",
        active: false,
    },
    {
        icon: "mdi-account",
        title: "帳號管理",
        subitems: [],
        to: "/accountList",
        active: false,
        admin: true
    },
]


const state = {
    status: { loginIn: (user != null) },
    user: user,
    isAdmin: (user ? user.account.role == 0 : false),
    menu: [],
    userlist: [],
    pagination: {
        currentPage: 1,
        totalItem: 0,
        totalPage: 0
    },
    showView: "",
    selectedUser: {},
}

// : {
//     status: { loginIn: true },
//     user: null,
//     isAdmin: false,
//     menu: []
// };

const actions = {
    login({ dispatch, commit }, { account, password }) {
        // commit('loginRequest', { username });
        userService.login(account, password)
            .then(
                data => {
                    // commit('loginSuccess', user);
                    // router.push('/');
                    console.log(TAG, data);
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let user = data.data;

                        if (user.token) {
                            console.log(TAG, user.token);
                            localStorage.setItem('user', JSON.stringify(user));
                            commit('loginSuccess', user)
                            dispatch("getMenu", null)
                        }


                    } else if (data.code == config.RES_CODE.LOGIN_FAIL) {
                        commit('loginFailure')
                    }
                },
                error => {
                    // commit('loginFailure', error);
                    // dispatch('alert/error', error, { root: true });
                    console.log(TAG, error);
                }
            );
    },
    logout({ commit }) {
        userService.logout();
        commit('logout');
    },
    getMenu({ commit, state }) {
        let menutemp = []
        menu.forEach(item => {
            if (item.admin != null && item.admin) {
                if (state.isAdmin) {
                    let sub = []
                    item.subitems.forEach(item => {
                        if (item.admin != null && item.admin) {
                            if (state.isAdmin) {
                                sub.push(item);
                            }
                        } else {
                            sub.push(item);
                        }
                    })
                    let temp = item
                    temp.subitems = sub
                    menutemp.push(temp)
                }
            } else {
                let sub = []
                item.subitems.forEach(item => {
                    if (item.admin != null && item.admin) {
                        if (state.isAdmin) {
                            sub.push(item);
                        }
                    } else {
                        sub.push(item);
                    }
                })
                let temp = item
                temp.subitems = sub
                menutemp.push(temp)
            }
        })
        commit('setMenu', menutemp)
    },
    getUserList({ commit, rootState }, { currentPage, count }) {
        let token = rootState.userModule.user.token
        userService.getUsersList(token, currentPage, count)
            .then(
                data => {
                    console.log(TAG, "get getUserList data :", data);
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data;
                        if (user.token) {
                            commit("setPagination", {
                                currentPage: currentPage,
                                totalItem: payload.totalCount,
                                totalPage: Math.ceil(payload.totalCount / count)
                            })
                            commit('setUserList', payload.data)
                        }
                    } else if (data.code == config.RES_CODE.LOGIN_FAIL) {
                        commit('loginFailure')
                    }
                },
                error => {
                    // commit('loginFailure', error);
                    // dispatch('alert/error', error, { root: true });
                    console.log(TAG, error);
                }
            );
    },
    showCreateView({ commit }, view) {
        commit('setView', view);
    },
    showInfoView({ commit }, { view, user }) {
        commit('setSelectedUser', user);
        commit('setView', view);
    },
    closeView({ commit }) {
        commit('setView', "");
    },
    editUserInfo({ commit, rootState }, user) {
        let token = rootState.userModule.user.token
        userService.editUserInfo(token, user)
            .then(
                data => {
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let user = data.data;
                        console.log(TAG, "editUserInfo:", user);
                        commit('setSelectedUser', user);
                    }
                },
                error => {
                    console.log(TAG, error);
                }
            );
    },
    createUser({ commit, rootState, dispatch, state }, user) {
        let token = rootState.userModule.user.token
        userService.createUser(token, user)
            .then(
                data => {
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let user = data.data;
                        console.log(TAG, "createUser:", user)
                        commit('setView', "")
                        dispatch("getUserList", state.pagination.currentPage)
                    }
                },
                error => {
                    console.log(TAG, error);
                }
            );
    }
};


const mutations = {
    loginSuccess(state, user) {
        state.status = { loginIn: true };
        state.user = user;
        state.isAdmin = (user.account.role == 0);
    },
    loginFailure(state) {
        state.status = {};
        state.user = null;
        state.isAdmin = false;
    },
    logout(state) {
        state.status = {};
        state.user = null;
        state.isAdmin = false;
    },
    setMenu(state, m) {
        state.menu = m;
    },
    setUserList(state, users) {
        state.userlist = users;
    },
    setPagination(state, payload) {
        state.pagination = payload;
    },
    setView(state, view) {
        state.showView = view
    },
    setSelectedUser(state, user) {
        state.selectedUser = user
    }
};

export const userModule = {
    namespaced: true,
    state,
    actions,
    mutations
};