<script>
import { mapActions, mapState } from "vuex";
import MapBreadcrumbs from "../plugins/MapBreadcrumbs";

export default {
  status: "mapUrgent",
  created() {
    this.getAllTownUrgentsByCitys(["015"]);
    this.setTwoBreadCrumb(this.viewTag);
  },
  components: {
    MapBreadcrumbs,
  },
  data() {
    return {
      viewTag: { tag: "高雄縣市", map: "mapKaohsiung" },
    };
  },
  computed: {
    ...mapState("doorsStatusModule", ["urgentCitys"]),
    ...mapState("websocketModule", ["haveNewData"]),
  },
  methods: {
    ...mapActions("doorsStatusModule", ["getAllTownUrgentsByCitys"]),
    ...mapActions("mapModule", ["setTwoBreadCrumb"]),
    checkNeedAssign(city_id) {
      let total = this.urgentCitys[city_id].totalCount;
      let assign = this.urgentCitys[city_id].assignCount;
      return assign == total;
    },
    checkAreas(ids) {
      let totalCount = 0;
      let assignCount = 0;
      Object.keys(this.urgentCitys).forEach((item) => {
        ids.forEach((id) => {
          if (item == id) {
            totalCount = totalCount + this.urgentCitys[item].totalCount;
            assignCount = assignCount + this.urgentCitys[item].assignCount;
          }
        });
      });
      return totalCount > 0
        ? (this.urgentCitys["combin1"] = { totalCount, assignCount })
        : null;
    },
  },
  watch: {
    urgentCitys() {
      this.checkAreas(["city_800", "city_801", "city_802", "city_803"]);
    },
    haveNewData() {
      this.getAllTownUrgentsByCitys(["015"]);
      this.setTwoBreadCrumb(this.viewTag);
    },
  },
};
</script>
