<style scope>
.v-main {
  padding: 80px 0px 0px !important;
}
.v-main__wrap {
  position: absolute;
  right: 0;
  top: 0;
  /* padding-top: 80px; */
}
.v-application--wrap {
  min-height: auto !important;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.text-red {
  color: red;
  position: relative;
}
.text-red:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: red;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.pageinfo {
  width: 80%;
  max-width: 900px;
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: -320px;
}
.theme--light.v-btn.btn_add {
  color: #fff;
}
</style>
<template>
  <v-container>
    <v-row class="mt-4">
      <v-col cols="4">
        <v-text-field
          rounded
          placeholder="序號"
          append-icon="mdi-magnify"
          xsmall
          filled
          background-color="#F3F3F3"
          color="#333333"
          dense
          height="42"
        ></v-text-field>
      </v-col>
      <v-col cols="8"></v-col>
      <v-col cols="12" class="text-center">
        <!-- <v-app id="inspire"> -->
        <v-simple-table>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-center">序號</th>
                <th class="text-center" width="100">委派狀態</th>
                <th class="text-center">名稱</th>
                <th class="text-center">委派車輛</th>
                <th class="text-center">委派時間</th>
                <th class="text-center">Actions</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in assignHistoryList" :key="item.id">
                <td>{{ item.sn }}</td>
                <td><Doorstatus :doorStatus="item.doorStatus" /></td>
                <td>{{ item.name }}</td>
                <td>{{ item.car_plate }}</td>
                <td>{{ setTime(item.assigned_date) }}</td>
                <td>
                  <v-btn @click="openInfoList(item)">詳細內容</v-btn>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <!-- </v-app> -->
      </v-col>
      <div class="pageinfo">
        <v-container class="max-width">
          <v-pagination
            v-model="pagination.currentPage"
            class="my-4"
            :length="pagination.totalPage"
            @input="handlePageChange"
          ></v-pagination>
        </v-container>
      </div>
    </v-row>
    <AssignInfo :initEdit="false"/>
    <!-- <AssignCreate /> -->
    <!-- <component :is="dailogComponent" ></component> -->
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
import moment from "moment";
import Doorstatus from "./plugins/DoorStatus";
import AssignInfo from "./AssignInfo";
// import AssignCreate from "./AssignCreate";

export default {
  status: "AssignRecord",
  components: {
    Doorstatus,
    AssignInfo,
  },
  data() {
    return {
      dialog: false
    };
  },
  created() {
    this.getAssignHistoryList(this.pagination.currentPage);
  },
  methods: {
    ...mapActions("historyModule", ["getAssignHistoryList"]),
    ...mapActions("assignModule", ["showAssignInfoDailog","setAssignInfoPage"]),
    openInfoList(assign){
      this.setAssignInfoPage(assign)
      this.showAssignInfoDailog(true)
    },
    setTime(time_str){
      return moment(time_str).format("YYYY-MM-DD")
    },
    handlePageChange(currentPage) {
      this.getAssignHistoryList(currentPage);
    },
  },
  computed: {
    ...mapState("historyModule", ["assignHistoryList", "pagination"]),
  },
};
</script>
