<script>
import { mapActions, mapState } from "vuex";
import MapBreadcrumbs from "../plugins/MapBreadcrumbs";

export default {
  status: "mapUrgent",
  created() {
    this.getAllUrgentsByCitys(["007", "008", "009", "010", "011"]);
    this.setOneBreadCrumb(this.viewTag)
  },
  components: {
    MapBreadcrumbs
  },
  data() {
    return {
      viewTag:{tag:'中部',map:'mapTaiwanCenter'}
    };
  },
  computed: {
    ...mapState("doorsStatusModule", ["urgentCitys"]),
    ...mapState("websocketModule", ["haveNewData"])
  },
  methods: {
    ...mapActions("doorsStatusModule", [
      "getAllUrgentsByCitys",
      "selectUrgentCity",
    ]),
    ...mapActions("mapModule", [
      "showTownshipView",
      "setOneBreadCrumb"
    ]),
    mouseOver: function() {
      // this.active = !this.active;
      console.log("aaa");
    },
  },
  watch:{
    haveNewData() {
      this.getAllUrgentsByCitys(["007", "008", "009", "010", "011"]);
      this.setOneBreadCrumb(this.viewTag)
    },
  }
};
</script>
