// import router from '../router/index'
// const TAG = "WebSocketModule"
import ReconnectingWebSocket from 'reconnecting-websocket';
import { statusConvert } from "../plugins/statusConvert";

const state = {
    socket: {
        isConnected: false,
        message: '',
        reconnectError: false,
    },
    wsclient: null,
    haveNewData: new Date().getTime()
}

const actions = {
    initWebSocket({ commit }) {

        //console.log("ReconnectingWebSocket: 'ws://127.0.0.1:9090' " + process.env.VUE_APP_WEBSOCKET_URL)
        const ws = new ReconnectingWebSocket(process.env.VUE_APP_WEBSOCKET_URL);
        ws.onopen = function() {
            console.log("ws open")
            commit("SOCKET_ONOPEN", ws)
        }
        ws.onclose = function() {
            // console.log("ws open")
            commit("SOCKET_ONCLOSE")
        }
        ws.onmessage = function(event) {
            // console.log("data", event)
            // let nowpath = router.currentRoute.name
            commit("NEED_GET_NEW_DATA", new Date().getTime())

            let door = JSON.parse(event.data)
            let n = new Notification("捲門狀況:", {
                body: "捲門序號:" + door.sn + "\r捲門:" + door.name + "\r目前狀態:" + statusConvert.getStatusTitle(parseInt(door.status))
            });
            n.onshow = function() {
                setTimeout(() => n.close(), 5000);
            }
        }
        ws.onerror = function(err) {
            commit("SOCKET_ONERROR", err)
        }

    },
    getDataSuccess({ commit }) {
        commit("GET_NEW_DATA")
    }
};

const mutations = {
    SOCKET_ONOPEN(state, event) {
        state.wsclient = event
        state.socket.isConnected = true
    },
    SOCKET_ONCLOSE(state) {
        state.socket.isConnected = false
    },
    SOCKET_ONERROR(state, event) {
        console.error(state, event)
    },
    // default handler called for all methods
    SOCKET_ONMESSAGE(state, message) {
        state.socket.message = message
    },
    // mutations for reconnect methods
    SOCKET_RECONNECT(state, count) {
        console.info(state, count)
    },
    SOCKET_RECONNECT_ERROR(state) {
        state.socket.reconnectError = true;
    },
    GET_NEW_DATA(state) {
        state.haveNewData = false
    },
    NEED_GET_NEW_DATA(state, time) {
        console.log("NEED_GET_NEW_DATA time", time)
        state.haveNewData = time
    }
};

export const websocketModule = {
    namespaced: true,
    state,
    actions,
    mutations
};