<script>
import { mapActions, mapState } from "vuex";
import MapBreadcrumbs from "../plugins/MapBreadcrumbs";

export default {
  status: "mapUrgent",
  created() {
    this.getAllTownUrgentsByCitys(["016"]);
    this.setTwoBreadCrumb(this.viewTag)
  },
  components: {
    MapBreadcrumbs
  },
  data() {
    return {
      viewTag:{tag:'屏東縣', map:'mapPingtung'}
    };
  },
  computed: {
    ...mapState("doorsStatusModule", ["urgentCitys"]),
    ...mapState("websocketModule", ["haveNewData"]),
  },
  methods: {
    ...mapActions("doorsStatusModule", ["getAllTownUrgentsByCitys"]),
    ...mapActions("mapModule", ["setTwoBreadCrumb"]),
    checkNeedAssign(city_id) {
      let total = this.urgentCitys[city_id].totalCount;
      let assign = this.urgentCitys[city_id].assignCount;
      return assign == total;
    },
  },
  watch: {
    haveNewData() {
      this.getAllTownUrgentsByCitys(["016"]);
      this.setTwoBreadCrumb(this.viewTag);
    },
  },
};
</script>
