<style scope>
.menu .v-tab--active {
  background-color: #fff !important;
  color: #b4b1a1 !important;
}
.menu .v-tab {
  position: relative;
}
.menu .v-tab:after {
  content: "";
  width: 1px;
  height: 20px;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -10px;
  background-color: #b4b1a1;
}
.menu .v-tab:last-child:after {
  display: none;
}
.menu .v-tabs-slider {
  display: none !important;
}
.submenu .v-tab {
  border: 1px solid #b4b1a1 !important;
}
.submenu .v-tab--active {
  background-color: #b4b1a1 !important;
  color: #ffffff !important;
}
.submenu .v-tabs-slider {
  display: none;
}

g path {
  transition: 0.5s;
  fill: #e2dfcf;
  stroke: #ffffff;
  stroke-width: 2px;
}
g path:hover, g path.active{
  fill: #b4b1a1;
  cursor: pointer;
}
g > g{
  position: relative;
}
g > g text{
   dominant-baseline:middle;
   text-anchor:middle;
     cursor: default;
     pointer-events: none;
     display: inline-block;
  
}
g > g path{
  fill: red;
}
g > g path.green{
  fill: green;
}
g > g path, g > g path text tspan {
  cursor: pointer;
  display: block;
}
text {
  position: relative;
}

.map {
  width: 100%;
  height: 100%;
  position: relative;
}
.map ul.areaname {
  list-style-type: decimal;
  font-size: 14px;
  line-height: 20px;
  position: absolute;
  top: 50px;
  right: 50px;
  /* transform: translateX(-50%); */
}
.breadcrumbs > *{
  display: inline-block;
}
.breadcrumbs .area, .breadcrumbs .city{
  color: #000;
  transition: all .3s;
}
.breadcrumbs .area:hover, .breadcrumbs .city:hover{
  cursor: pointer;
  color: #e2dfcf;
  text-decoration: underline;
}
</style>
<template>
  <v-container>
    <v-row>
      <v-col cols="8" offset="2" class="mt-4">
        <v-tabs
          color="#EFEEE9"
          centered
          class="menu"
          @change="handleAreaChange"
          v-model="switchArea"
        >
          <v-tabs-slider color="#B4B1A1"></v-tabs-slider>
          <v-tab>北部</v-tab>
          <v-tab>中部</v-tab>
          <v-tab>南部</v-tab>
          <v-tab>東部</v-tab>
        </v-tabs>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="8" offset="2">
        <v-tabs
          color="#EFEEE9"
          centered
          height="40px"
          class="submenu"
          v-model="switchType"
          @change="handleShowTypeChange"
        >
          <v-tab v-for="item in items" :key="item">{{ item }}</v-tab>
        </v-tabs>
      </v-col>
    </v-row>

    <component :is="showCityView"></component>



    <!-- 清單 
    <v-tabs-items v-model="selectedtype">
      <v-tab-item>
        <component :is="showCityView"></component>
      </v-tab-item>
      <v-tab-item>
        <DoorMapList />
      </v-tab-item>
    </v-tabs-items>-->
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";

import DoorMapList from "./DoorMapList";
import mapTaiwan from "./maps/mapTaiwan";
import mapTaiwanNorth from "./maps/mapTaiwanNorth";
import mapTaiwanCenter from "./maps/mapTaiwanCenter";
import mapTaiwanSouth from "./maps/mapTaiwanSouth";
import mapTaiwanEast from "./maps/mapTaiwanEast";
import mapTaipei from "./maps/mapTaipei";
import mapTaoyuan from "./maps/mapTaoyuan";
import mapHsinchu from "./maps/mapHsinchu";
import mapMiaoli from "./maps/mapMiaoli";
import mapTaichung from "./maps/mapTaichung";
import mapChanghua from "./maps/mapChanghua";
import mapNantou from "./maps/mapNantou";
import mapYunlin from "./maps/mapYunlin";
import mapChiayi from "./maps/mapChiayi";
import mapTainan from "./maps/mapTainan";
import mapKaohsiung from "./maps/mapKaohsiung";
import mapPingtung from "./maps/mapPingtung";
import mapYilan from "./maps/mapYilan";
import mapHualien from "./maps/mapHualien";
import mapTaitung from "./maps/mapTaitung";
export default {
  status: "mapArea",
  created() {
      let { selectedArea, selectedType } = this;
      this.getData({ selectedArea, selectedType });
  },
  components: {
    DoorMapList,
    mapTaiwan,
    mapTaiwanNorth,
    mapTaiwanCenter,
    mapTaiwanSouth,
    mapTaiwanEast,
    mapTaipei,
    mapTaoyuan,
    mapHsinchu,
    mapMiaoli,
    mapTaichung,
    mapChanghua,
    mapNantou,
    mapYunlin,
    mapChiayi,
    mapTainan,
    mapKaohsiung,
    mapPingtung,
    mapYilan,
    mapHualien,
    mapTaitung,
  },
  data() {
    return {
      selectarea: 0,
      selectedtype: 0,
      tab: null,
      items: ["區域圖", "清單"],
    };
  },
  methods: {
    ...mapActions("mapModule", ["getData","setAreaMapView","showAreaListView","showAreaMapView"]),
    handleAreaChange() {
        switch (this.selectedArea) {
            case 0:
                this.setAreaMapView("mapTaiwanNorth")
                break;
            case 1:
                this.setAreaMapView("mapTaiwanCenter")
                break;
            case 2:
                this.setAreaMapView("mapTaiwanSouth")
                break;
        }
    },
    handleShowTypeChange() {
        switch (this.selectedType) {
            case 0:
                this.showAreaMapView()
                break;
            case 1:
                this.showAreaListView()
                break;
        }
    },
  },
  computed: {
    ...mapState("mapModule", ["selectedArea", "selectedType", "showCityView"]),
    switchArea:{
        get () {
          return this.selectedArea
        },
        set (value) {
          this.$store.commit('mapModule/setSelectedArea', value)
        }
    },
    switchType:{
        get () {
          return this.selectedType
        },
        set (value) {
          this.$store.commit('mapModule/setSelectedType', value)
        }
    }
  },
  watch: {},
};
</script>
