<style scoped>
.map,
iframe {
  width: 100%;
  height: 300px;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -6px;
}
.btn_close {
  position: absolute;
  right: 0;
  top: 0;
}
.item_title {
  width: 100px;
  display: inline-block;
  padding: 12px 16px;
  background-color: #f5f5f5;
  height: 48px;
  line-height: 24px;
  vertical-align: middle;
}
.item_title.date {
  width: 160px;
}
.cross {
  width: 25px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.unit {
  width: 44px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 12px 16px !important; */
}
.item_content {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  height: 46px;
  line-height: 24px;
  vertical-align: middle;
  margin-top: 0;
}
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 8px !important; */
  vertical-align: middle;
  margin-top: 0;
}
.item_content.city,
.item_content.village {
  width: calc((100% - 125px) / 2);
}
.item_content.city {
  margin-right: 25px;
}
.item_content.road {
  width: 100%;
}
.item_content.size {
  width: calc((100% - 257px) / 2);
}
.item_content.date {
  width: calc(100% - 160px);
}
.col {
  padding-top: 0 !important;
}
.theme--light.v-data-table
  > .v-data-table__wrapper
  > table
  > tbody
  > tr:hover:not(.v-data-table__expanded__content):not(.v-data-table__empty-wrapper) {
  background-color: #fff;
}
.status {
  height: 158px;
}
.v-card__actions {
  padding-bottom: 20px;
}
.v-card__actions > .v-btn.v-btn {
  margin: 0 auto;
}
.v-input__slot {
  width: 0 !important;
}
.tableContent table {
  border: 1px solid #efeee9;
}
.tableContent table th {
  background: #efeee9;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff !important;
}
.tableContent table th:last-child {
  border-right: 1px solid #efeee9;
}
.tableContent table td {
  border-right: 1px solid #efeee9;
  border-bottom: 1px solid #efeee9;
}
.tableContent table td:first-child {
  border-left: 1px solid #efeee9;
}
</style>

<template>
  <v-row justify="center">
    <v-dialog value="true" persistent max-width="540px">
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-card>
          <v-card-title>
            <h3 class="text-center pa-6 mx-auto">帳號資料</h3>
          </v-card-title>

          <v-card class="overflow-hidden" color flat>
            <v-card-text>
              <v-row>
                <v-col cols="12" class="item">
                  <span class="item_title">帳號</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="edit.account"
                    :rules="formRules.DoorNameRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedUser.account
                  }}</v-card-text>
                </v-col>
                <v-col cols="12" class="item">
                  <span class="item_title">密碼</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="edit.passWord"
                    :rules="formRules.DoorNameRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedUser.passWord
                  }}</v-card-text>
                </v-col>
                <v-col cols="12" class="item">
                  <span class="item_title">姓名</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="edit.name"
                    :rules="formRules.DoorNameRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedUser.name
                  }}</v-card-text>
                </v-col>
                <v-col cols="12" class="item">
                  <span class="item_title">電話</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="edit.phone"
                    :rules="formRules.taxRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedUser.phone
                  }}</v-card-text>
                </v-col>
                <v-col cols="12" class="item">
                  <span class="item_title">電子信箱</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="edit.email"
                    :rules="formRules.addressRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedUser.email
                  }}</v-card-text>
                </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions align-center>
              <v-btn v-if="isEditing" @click="confirmEdit()" color="success">
                確認編輯
              </v-btn>
              <v-btn v-else @click="enterEdit" color="blue-grey lighten-4">
                編輯資料
              </v-btn>
            </v-card-actions>
            <v-snackbar v-model="hasSaved" :timeout="2000" absolute bottom left
              >Your profile has been updated</v-snackbar
            >
          </v-card>
          <v-card-actions class="btn_close">
            <v-icon color="black" text @click="closeInfoPage"
              >mdi-close-circle</v-icon
            >
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
  </v-row>
</template>
<script>
import { mapActions, mapState } from "vuex";
import { formRules } from "../plugins/formRules";

export default {
  status: "AccountInfo",
  components:{
  },
  data() {
    return {
      hasSaved: false,
      isEditing: false,
      edit: {},
      valid: false,
      formRules: formRules,
    };
  },
  created() {
    console.log("UserInfoPage")
  },
    computed: {
    ...mapState("userModule", ["selectedUser"]),
  },
  methods: {
    ...mapActions("userModule", ["closeView","editUserInfo"]),
    enterEdit() {
      this.isEditing = !this.isEditing;
      this.edit = { ...this.selectedUser };
    },
    confirmEdit() {
      let validate = this.$refs.form.validate();
      if (validate) {
        this.isEditing = !this.isEditing;
        this.editUserInfo(this.edit);
      }
    },
    closeInfoPage() {
      this.closeView();
    }
  },

};
</script>
