<style scoped>
.map,
iframe {
  width: 100%;
  height: 300px;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.btn_close {
  position: absolute;
  right: 0;
  top: 0;
}
.item_title {
  width: 100px;
  display: inline-block;
  padding: 12px 16px;
  background-color: #f5f5f5;
  height: 48px;
  line-height: 24px;
  vertical-align: middle;
}
.item_title.date {
  width: 160px;
}
.cross {
  width: 25px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.unit {
  width: 44px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.item_content,
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 12px 16px !important; */
}
.item_content {
  height: 46px;
  line-height: 24px;
  vertical-align: middle;
  margin-top: 0;
}
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 8px !important; */
  vertical-align: middle;
  margin-top: 0;
}
.item_content.city,
.item_content.village {
  width: calc((100% - 125px) / 2);
}
.item_content.city {
  margin-right: 25px;
}
.item_content.road {
  width: 100%;
}
.item_content.size {
  width: calc((100% - 257px) / 2);
}
.item_content.date {
  width: calc(100% - 160px);
}
.col {
  padding-top: 0 !important;
}
.theme--light.v-data-table
  > .v-data-table__wrapper
  > table
  > tbody
  > tr:hover:not(.v-data-table__expanded__content):not(.v-data-table__empty-wrapper) {
  background-color: #fff;
}
.status {
  height: 158px;
}
.v-card__actions {
  padding-bottom: 20px;
}
.v-card__actions > .v-btn.v-btn {
  margin: 0 auto;
}
</style>

<template>
  <v-row justify="center">
    <v-dialog value="true" persistent max-width="540px">
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-card>
          <v-card class="overflow-hidden" color flat>
          <v-card-title>
            <h3 class="text-center pa-6 mx-auto">新增帳號</h3>
          </v-card-title>
            <v-card-text>
              <v-row>
                <v-col cols="12" class="item">
                  <span class="item_title">帳號</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="user.account"
                    :rules="formRules.AccountRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" class="item">
                  <span class="item_title">密碼</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="user.passWord"
                    :rules="formRules.PasswordRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" class="item">
                  <span class="item_title">姓名</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="user.name"
                    :rules="formRules.DoorNameRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" class="item">
                  <span class="item_title">電話</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="user.phone"
                    :rules="formRules.taxRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" class="item">
                  <span class="item_title">電子信箱</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="user.email"
                    :rules="formRules.emailRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions align-center>
              <v-btn @click="confirm" color="success">
                確認
              </v-btn>
            </v-card-actions>
            <!-- <v-snackbar value="false" :timeout="2000" absolute bottom left
            >Your profile has been updated</v-snackbar
            > -->
          </v-card>
          <v-card-actions class="btn_close">
            <v-icon color="black" text @click="closeDailog"
              >mdi-close-circle</v-icon
            >
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
  </v-row>
</template>
<script>
import { mapActions, mapState } from "vuex";
import { selectsItems } from "../plugins/SelectsItems";
import { formRules } from "../plugins/formRules";

export default {
  status: "AccountCreate",
  created() {
  },
  data() {
    return {
      door_contract: selectsItems.contractSelectsItems,
      door_material: selectsItems.materialSelectsItems,
      door_style: selectsItems.typeSelectsItems,
      formRules: formRules,
      valid: false,
      user: {
        account: "",
        passWord: "",
        name: "",
        phone: "",
        email: "",
      },
      datePickerMenu: false,
    };
  },
  methods: {
    ...mapActions("userModule", ["closeView","createUser"]),
    confirm() {
      let validate = this.$refs.form.validate();
      if (validate) {
        this.createUser(this.user)
      }
    },
    closeDailog() {
      this.closeView();
    },
  },
  computed: {
    ...mapState("carModule", []),
  },
  watch: {
    // isShowCreateDoor(isShow) {
    //   if (isShow) {
    //     this.$refs.form.reset()
    //     
    //   }
    // },
  },
};
</script>
