// import { doorService } from '../service/door.service.js';
// import config from '../config'

const TAG = "MapModule"
const state = {
    selectedArea: 0,
    selectedType: 0,
    showCityView: "mapTaiwanNorth",
    selectMapTag: "mapTaiwanNorth",
    breadCrumbs: { one: { tab: '', map: '' }, two: null }
}

const actions = {
    getData({ dispatch, commit }, { selectedArea, selectedType }) {
        console.log(TAG, "getDatagetDatagetData", selectedArea, selectedType);
        if (selectedType == 0) {
            dispatch("getStatesfromArea", selectedArea)
                // commit('setView', state.selectMapTag)
        } else {
            // dispatch("getStatesfromType", selectarea)
            commit('setView', "DoorMapList")
        }
    },
    showAreaMapView({ commit, state, dispatch }) {
        dispatch("doorsModule/clearDoorsList", null, { root: true })
        commit('setView', state.selectMapTag)
    },
    setAreaMapView({ commit, dispatch }, view) {
        dispatch("doorsModule/clearDoorsList", null, { root: true })
        commit('setMapView', view)
        commit('setSelectedType', 0)
    },
    showAreaListView({ commit }) {
        commit('setView', "DoorMapList")
        commit('setSelectedType', 1)
    },
    showAreaListViewByCitys({ commit, dispatch }, cityids) {
        dispatch("doorsStatusModule/getAllTownUrgentsByCitys", cityids, { root: true })
        commit('setView', "DoorMapList")
        commit('setSelectedType', 1)
    },
    getStatesfromArea({ commit /*, dispatch */ }, selectarea) {
        switch (selectarea) {
            case 0:
                // console.log(TAG, "mapTaiwanNorth");
                commit('setMapView', "mapTaiwanNorth")
                break;
            case 1:
                // console.log(TAG, "mapTaiwanCenter");
                commit('setMapView', "mapTaiwanCenter")
                break;
            case 2:
                // console.log(TAG, "mapTaiwanSouth");
                commit('setMapView', "mapTaiwanSouth")
                break;
        }
    },
    getStatesfromType({ dispatch }, selectarea) {
        switch (selectarea) {
            case 0:
                dispatch("doorsModule/getDoorsByCitysAndStatuses", { city_ids: ["001", "002", "003", "004", "005", "006"], statuses: [1, 2, 4, 5], currentPage: 1 }, { root: true })
                break;
            case 1:
                dispatch("doorsModule/getDoorsByCitysAndStatuses", { city_ids: ["007", "008", "009", "010", "011"], statuses: [1, 2, 4, 5], currentPage: 1 }, { root: true })
                break;
            case 2:
                dispatch("doorsModule/getDoorsByCitysAndStatuses", { city_ids: ["012", "013", "014", "015", "016"], statuses: [1, 2, 4, 5], currentPage: 1 }, { root: true })
                break;
        }
    },
    showTownshipView({ commit /*, dispatch*/ }, town) {
        commit('setMapView', town)
    },
    setMapTag({ commit }, tag) {
        commit('setMapTag', tag)
    },
    setSelectedCityIds({ commit /*, dispatch*/ }, ids) {
        commit('setCityIds', ids)
    },
    setOneBreadCrumb({ commit }, breadcrumb) {
        commit('setoneBreadCrumb', breadcrumb)
    },
    setTwoBreadCrumb({ commit }, breadcrumb) {
        commit('settwoBreadCrumb', breadcrumb)
    }
};

const mutations = {
    setView(state, view) {
        state.showCityView = view;
    },
    setMapView(state, view) {
        state.showCityView = view;
        state.selectMapTag = view;
    },
    setMapTag(state, tag) {
        state.selectMapTag = tag;
    },
    setoneBreadCrumb(state, breadcrumb) {
        console.log(TAG, "setoneBreadCrumb", breadcrumb);
        state.breadCrumbs.one = breadcrumb;
        state.breadCrumbs.two = null;
    },
    settwoBreadCrumb(state, breadcrumb) {
        state.breadCrumbs.two = breadcrumb;
    },
    setSelectedArea(state, area) {
        state.selectedArea = area
    },
    setSelectedType(state, type) {
        state.selectedType = type
    }
};

export const mapModule = {
    namespaced: true,
    state,
    actions,
    mutations
};