import axios from "axios";
import router from '../router/index'

const URL = "user/";

export const userService = {
    login,
    logout,
    getUsersList,
    editUserInfo,
    createUser
};

function login(username, password) {
    return axios
        .post(process.env.VUE_APP_API_URL + URL + "login", {
            account: username,
            passWord: password,
        })
        .catch(function(error) {
            return error;
        })
        .then(function(response) {
            if (response == null) return null;
            return response.data;
        })
}

function logout() {
    localStorage.removeItem("user");
    router.push("/").catch((err) => {
        err;
    });
}

function getUsersList(token, currentPage, count) {
    return axios
        .get(process.env.VUE_APP_API_URL + URL, {
            headers: { 'authorization': token },
            params: {
                currentPage: currentPage,
                count: count
            },
        })
        .then(function(response) {
            if (response == null) return null;
            return response.data;
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}

function createUser(token, user) {
    return axios
        .post(process.env.VUE_APP_API_URL + URL, user, {
            headers: { 'authorization': token },
        })
        .then(function(response) {
            if (response == null) return null;
            return response.data;
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}


function editUserInfo(token, user) {
    return axios
        .put(process.env.VUE_APP_API_URL + URL, user, {
            headers: { 'authorization': token },
        })
        .then(function(response) {
            if (response == null) return null;
            return response.data;
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}