import axios from "axios";

const URL = "car/";
const TAG = "carService"

export const carService = {
    create,
    getAllCar,
    closeAssign,
    editCar,
    getCars
    // delete: _delete
};

function create(token, car) {
    return axios.post(process.env.VUE_APP_API_URL + URL + 'add', car, {
            headers: { 'authorization': token },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}

function editCar(token, car) {
    return axios.put(process.env.VUE_APP_API_URL + URL + 'edit', car, {
            headers: { 'authorization': token }
        })
        .then(response => {
            console.log(TAG, "editCar:", response)
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}

function getCars(token, currentPage) {
    return axios.get(process.env.VUE_APP_API_URL + URL + 'list', {
            headers: { 'authorization': token },
            params: {
                currentPage: currentPage
            },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}


function getAllCar(token) {
    return axios.get(process.env.VUE_APP_API_URL + URL + 'list', {
            headers: { 'authorization': token },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}



function closeAssign(token, assign) {
    return axios.put(process.env.VUE_APP_API_URL + URL + 'close', assign, {
            headers: { 'authorization': token },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}