<style scope>
.v-main {
  padding: 80px 0px 0px !important;
}
.v-main__wrap {
  position: absolute;
  right: 0;
  top: 0;
  /* padding-top: 80px; */
}
.v-application--wrap {
  min-height: auto !important;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.text-red {
  color: red;
  position: relative;
}
.text-red:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: red;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.pageinfo {
  width: 80%;
  max-width: 900px;
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: -320px;
}
.theme--light.v-btn.btn_add {
  color: #fff;
}
</style>
<template>
  <v-container>
    <v-row class="mt-4">
      <v-col cols="4">
        <v-text-field
          rounded
          label=""
          placeholder="SIM卡序號"
          append-icon="mdi-magnify"
          xsmall
          filled
          background-color="#F3F3F3"
          color="#333333"
          dense
          height="42"
        ></v-text-field>
      </v-col>
      <v-col 
      cols="2"
      offset="6"
      >
        <div class="text-right">
          <v-btn 
            rounded
            color="#B4B1A1"
            class="btn_add" 
            @click="openCreateDailog"
          >
            <v-icon left>
              mdi-plus-circle
            </v-icon>
            新增車輛
          </v-btn>
        </div>
      </v-col>
      <v-col cols="12" class="text-center">
        <v-app id="inspire">
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-center" width="100">SIM卡序號</th>
                  <th class="text-center">車牌號碼</th>
                  <th class="text-center">備註</th>
                  <th class="text-center">Actions</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in carList" :key="item.id">
                  <td>{{ item.sim }}</td>
                  <td>{{ item.car_plate }}</td>
                  <td>{{ item.remarks }}</td>
                  <td>
                    <v-btn text @click="goToInfo(item)">詳細內容</v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-app>
      </v-col>
      <div class="pageinfo">
        <v-container class="max-width">
          <v-pagination
            v-model="pagination.currentPage"
            class="my-4"
            :length="pagination.totalPage"
            @input="handlePageChange"
          ></v-pagination>
        </v-container>
      </div>
    </v-row>
    <component :is="view" ></component>
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
import CarInfo from "./CarInfo";
import CarCreate from "./CarCreate";

export default {
  status: "CarList",
  created() {
    const { currentPage } = this.pagination;
    this.getCars( currentPage );
  },
  components: {
    CarInfo,
    CarCreate,
  },
  data() {
    return {
    };
  },
  methods: {
    ...mapActions("carModule", ["showView","getCars","selectedCar"]),
    goToInfo(car) {
      this.selectedCar(car);
      this.showView("CarInfo");
    },
    handlePageChange(currentPage) {
      this.getCars(currentPage);
    },
    openCreateDailog(){
      this.showView("CarCreate")
    }
  },
  computed: {
    ...mapState("carModule", ["carList", "pagination","view"]),
    dailogComponent() {
      return this.dialogTag;
    },
  },
};
</script>
