import axios from 'axios'
const TAG = "DoorsService"
const URL = "status/"

function getAllUrgentsByCity() {
    console.log(TAG, "Call getAllUrgentsByCity")
    return axios.get(process.env.VUE_APP_API_URL + URL + 'urgent')
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}

function getAllUrgentsByCitys(token, citys) {
    console.log(TAG, "Call getAllUrgentsByCitys:", citys)
    return axios.get(process.env.VUE_APP_API_URL + URL + 'area', {
            headers: { 'authorization': token },
            params: {
                citys: citys
            },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}

function getAllTownUrgentsByCitys(token, citys) {
    console.log(TAG, "Call getAllUrgentsByCitys:", citys)
    return axios.get(process.env.VUE_APP_API_URL + URL + 'area/city', {
            headers: { 'authorization': token },
            params: {
                citys: citys
            },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}
export const doorStatusService = {
    getAllUrgentsByCity,
    getAllUrgentsByCitys,
    getAllTownUrgentsByCitys
};