import { carService } from '../service/car.service';
import config from '../config'
// import { doorService } from '../service/door.service.js';

const TAG = "carModule"
const state = {
    carList: [],
    pagination: {
        currentPage: 1,
        totalItem: 0,
        totalPage: 0,
    },
    selected: {},
    edit: false,
    view: ""
}

const actions = {
    createNewCar({ commit, rootState, state, dispatch }, newCar) {
        let token = rootState.userModule.user.token
            // console.log(TAG, "createnewCar:", newCar)
        carService.create(token, newCar)
            .then(
                data => {
                    console.log(TAG, "Create Car:", data)
                    commit("setView", "")
                    dispatch("getAllCars", state.pagination.currentPage)
                },
                error => {
                    console.log(TAG, "Create door:", error)
                }
            )
    },
    editCar({ commit, rootState, }, car) {
        let token = rootState.userModule.user.token
        console.log(TAG, "editCar:", car)
        carService.editCar(token, car)
            .then(
                data => {
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        console.log(TAG, "set sucess:", payload)
                        commit("upCarToCarList", payload)
                    }
                },
                error => {
                    console.log(TAG, "edit door:", error)
                }
            )
    },
    getCars({ commit, rootState }, currentPage) {
        // console.log(TAG, "GetAllCars:", currentPage)
        let token = rootState.userModule.user.token
        carService.getCars(token, currentPage)
            .then(
                data => {
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        commit("setCarList", payload.data)
                        commit("setPagination", {
                            currentPage: currentPage,
                            totalItem: payload.totalCount,
                            totalPage: Math.ceil(payload.totalCount / 10)
                        })
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    getAllCars({ commit, rootState }) {
        let token = rootState.userModule.user.token
        carService.getAllCar(token)
            .then(
                data => {
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        commit("setCarList", payload.data)
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    selectedCar({ commit }, car) {
        console.log("selectedCar:", car);
        commit("setSelectedCar", car)
    },
    showView({ commit }, view) {
        console.log(TAG)
        commit("setView", view)
    },
    closeAllView({ commit }) {
        commit("setView", "")
    }
};

const mutations = {
    setCarList(state, list) {
        state.carList = list
    },
    setSelectedCar(state, car) {
        state.selected = car;
    },
    upCarToCarList(state, newCar) {
        const item = state.carList.find(item => item.id === newCar.id);
        Object.assign(item, newCar);
    },
    setPagination(state, payload) {
        state.pagination = payload;
    },
    setView(state, view) {
        state.view = view;
    },
};

export const carModule = {
    namespaced: true,
    state,
    actions,
    mutations
};