<style scope>
.v-main {
  padding: 80px 0px 0px !important;
}
.v-main__wrap {
  position: absolute;
  right: 0;
  top: 0;
  /* padding-top: 80px; */
}
.v-application--wrap {
  min-height: auto !important;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.text-red {
  color: red;
  position: relative;
}
.text-red:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: red;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.pageinfo {
  width: 80%;
  max-width: 900px;
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: -320px;
}
.theme--light.v-btn.btn_add {
  color: #fff;
}
</style>
<template>
  <v-container>
    <v-row class="mt-4">
      <v-col 
      cols="4"
      >
        <v-text-field
          rounded
          label=""
          placeholder="帳號"
          append-icon="mdi-magnify"
          xsmall
          filled
          background-color="#F3F3F3"
          color="#333333"
          dense
          height="42"
        ></v-text-field>
      </v-col>
      <v-col 
      cols="2"
      offset="6"
      >
        <div class="text-right">
          <v-btn
            rounded
            color="#B4B1A1"
            class="btn_add"
            @click="openCreateView"
          >
            <v-icon left>
              mdi-plus-circle
            </v-icon>
            新增帳號
          </v-btn>
        </div>
      </v-col>
      <v-col cols="12" class="text-center">
        <v-app id="inspire">
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr ref="titleDiv">
                  <th class="text-center" width="100">帳號</th>
                  <th class="text-center">密碼</th>
                  <th class="text-center">姓名</th>
                  <th class="text-center">電話</th>
                  <th class="text-center">電子信箱</th>
                  <th class="text-center">Actions</th>
                </tr>
              </thead>
              <tbody  ref="childDiv">
                <tr v-for="item in userlist" :key="item.id" >
                  <td>{{ item.account }}</td>
                  <td>{{ item.password }}</td>
                  <td>{{ item.name }}</td>
                  <td>{{ item.phone }}</td>
                  <td>{{ item.email }}</td>
                  <td>
                    <v-btn text @click="openUserInfo(item)">詳細內容</v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-app>
      </v-col>
      <div class="pageinfo">
        <v-container class="max-width">
          <v-pagination
            v-model="pagination.currentPage"
            class="my-4"
            :length="pagination.totalPage"
            @input="handlePageChange"
          ></v-pagination>
        </v-container>
      </div>
    </v-row>
    <component :is="showView"></component>
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
// import CarInfo from "./CarInfo";
import AccountCreate from "./AccountCreate";
import AccountInfo from "./AccountInfo";

export default {
  status: "AccountList",
  created() {

  },
  mounted(){
    // console.log("mounted:::::",this.$refs.childDiv)
    // console.log("this.$refs.childDiv",this.$refs.childDiv)
    // console.log("螢幕:" + (window.innerHeight - this.$refs.childDiv.getBoundingClientRect().y - this.$refs.childDiv.getBoundingClientRect().height))
    let contentAreaHeight = (window.innerHeight - this.$refs.childDiv.getBoundingClientRect().y - this.$refs.childDiv.getBoundingClientRect().height)/this.$refs.titleDiv.clientHeight
    // console.log("contentAreaHeight:", parseInt(contentAreaHeight))
    const { currentPage } = this.pagination;
    this.getUserList({currentPage,count: parseInt(contentAreaHeight)});
  },
  components: {
    AccountCreate,
    AccountInfo,
  },
  data() {
    return {};
  },
  methods: {
    ...mapActions("userModule", [
      "getUserList",
      "showCreateView",
      "showInfoView",
    ]),
    handlePageChange() {},
    openCreateView() {
      //this.showCreateView("AccountCreate");
    },
    openUserInfo(item) {
      let user = item;
      let view = "AccountInfo";
      this.showInfoView({ view, user });
    },
  },
  computed: {
    ...mapState("userModule", ["userlist", "pagination", "showView"]),
  },
};
</script>
