import { doorStatusService } from '../service/doorStatus.service';
import config from '../config'

const TAG = "DoorStatusModule"
const state = {
    urgentCitys: [],
    selectedUrgentCity: {},
    selectedUrgentCitys: [],
    showUrgentList: false,
    selectAreaCityIds: []
}

const actions = {
    getAllUrgentsByCity({ commit }) {
        commit("setUrgentCitys", [])
        doorStatusService.getAllUrgentsByCity()
            .then(
                data => {
                    console.log(TAG, "Get getAllUrgentsByCity:", data)
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        let new_data = {}
                        payload.forEach(data => new_data[data.city_id] = data)
                        console.log(TAG, "new_data:", new_data)
                        commit("setUrgentCitys", new_data)
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    getAllUrgentsByCitys({ commit, rootState }, citys) {
        commit("setUrgentCitys", [])
        commit("setCityIds", citys)
        let token = rootState.userModule.user.token
        doorStatusService.getAllUrgentsByCitys(token, citys)
            .then(
                data => {
                    console.log(TAG, "Get getAllUrgentsByCitys:", data)
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        let new_data = {}
                        payload.forEach(data => new_data[data.city_id] = data)
                        commit("setUrgentCitys", new_data)
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    getAllTownUrgentsByCitys({ commit, rootState }, citys) {
        commit("setUrgentCitys", [])
        commit("setCityIds", citys)
        let token = rootState.userModule.user.token
        console.log(TAG, "getAllTownUrgentsByCitys:", citys)
        doorStatusService.getAllTownUrgentsByCitys(token, citys)
            .then(
                data => {
                    console.log(TAG, "Get getAllTownUrgentsByCitys:", data)
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        let new_data = {}
                        payload.forEach(data => new_data[data.citystate_id] = data)
                        console.log(TAG, "new_data:", new_data)
                        commit("setUrgentCitys", new_data)
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    selectUrgentCity({ commit }, city) {
        commit("setUrgentCity", city)
    },
};

const mutations = {
    setUrgentCitys(state, citys) {
        state.urgentCitys = citys;
    },
    setUrgentCity(state, city) {
        state.selectedUrgentCity = city;
        state.showUrgentList = (city != null)
    },
    setShowUrgentList(state) {
        state.showUrgentList = true;
    },
    setCityIds(state, ids) {
        state.selectAreaCityIds = ids;
    },
};

export const doorsStatusModule = {
    namespaced: true,
    state,
    actions,
    mutations
};