<style scoped>
/* .v-main {
  padding: 80px 0px 0px !important;
} */
.v-main__wrap {
  position: absolute;
  right: 0;
  top: 0;
  /* padding-top: 80px; */
}
.v-application--wrap {
  min-height: auto !important;
}
.text-red {
  color: red;
  position: relative;
}
.text-orange {
  color: orange;
  position: relative;
}
.text-yellow {
  color: yellow;
  position: relative;
}
.text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.text-red:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: red;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.pageinfo {
  width: 80%;
  max-width: 900px;
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: -320px;
}
.theme--light.v-btn.btn_add {
  color: #fff;
}
</style>
<template>
  <v-container>
    <v-row class="mt-4">
      <v-col cols="4">
        <v-text-field
          rounded
          label=""
          placeholder="時間搜尋 / 捲門名稱"
          append-icon="mdi-magnify"
          xsmall
          filled
          background-color="#F3F3F3"
          color="#333333"
          dense
          height="42"
        ></v-text-field>
      </v-col>
      <v-col cols="2" offset="6">
        <div class="text-right">
          <v-btn
            rounded
            color="#B4B1A1"
            class="btn_add"
            @click="openCreateDailog"
          >
            <v-icon left>
              mdi-plus-circle
            </v-icon>
            新增捲門
          </v-btn>
        </div>
      </v-col>
      <v-col cols="12" class="text-center">
        <v-app id="inspire">
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th width="150">
                    <v-select
                      :items="doorstatusSelectsItems"
                      label="狀態"
                      item-text="name"
                      item-value="status"
                      v-model="statusfilter"
                      @change="getNewData"
                    ></v-select>
                  </th>
                  <th class="text-center">狀態更新時間</th>
                  <th class="text-center">捲門名稱</th>
                  <th class="text-center">地址</th>
                  <th class="text-center">Actions</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in doors" :key="item.sn">
                  <!-- <td :class="getStatusColor(item.status)">
                    {{ getStatusTitle(item.status) }}
                  </td> -->
                  <td>
                    <Doorstatus :doorStatus="item.status" />
                  </td>
                  <td>{{ formatdDate(item.updatedAt) }}</td>
                  <td>{{ item.name }}</td>
                  <td>{{ item.address }}</td>
                  <td>
                    <v-btn text @click="goToInfo(item)">詳細內容</v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-app>
      </v-col>
      <div class="pageinfo">
        <v-container class="max-width">
          <v-pagination
            v-model="currentPage"
            class="my-4"
            :length="pagination.totalPage"
            @input="getNewData"
          ></v-pagination>
        </v-container>
      </div>
    </v-row>
    <!-- <DoorCreate />
    <DoorInfo /> -->
    <!-- <DoorInfo :selected-door="selectedDoor" /> -->
    <component :is="showDialog"></component>
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
import DoorInfo from "./DoorInfo";
import DoorCreate from "./DoorCreate";
import Doorstatus from "./plugins/DoorStatus";
import moment from "moment";
import { selectsItems } from "../plugins/SelectsItems";

export default {
  status: "DoorList",
  created() {
    this.getNewData()
  },
  components: {
    DoorInfo,
    DoorCreate,
    Doorstatus,
  },
  data() {
    return {
      doorstatusSelectsItems: selectsItems.doorstatusSelectsItems,
      currentPage: 1,
      statusfilter: "all",
    };
  },
  methods: {
    ...mapActions("doorsModule", [
      "getAll",
      "chooseDoor",
      "showDailogView",
      "getAllDoorByStatus",
    ]),
    goToInfo(door) {
      this.chooseDoor(door);
      this.showDailogView("DoorInfo");
    },
    // handlePageChange() {
    //   this.getNewData()
    // },
    // handleChange() {
    //   this.getNewData()
    // },
    openCreateDailog() {
      this.showDailogView("DoorCreate");
    },
    formatdDate(date) {
      return moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
    getNewData(){
      const { currentPage } = this;
      const statusList = this.statusfilter;
      if (statusList == "all") {
        this.getAll({ currentPage });
      } else {
        this.getAllDoorByStatus({ statusList, currentPage });
      }
    }
  },
  computed: {
    ...mapState("doorsModule", ["doors", "pagination", "showDialog"]),
    ...mapState("websocketModule", ["haveNewData"]),
    dailogComponent() {
      return this.dialogTag;
    },
  },
  watch: {
    haveNewData() {
      this.getNewData()
    },
  },
};
</script>
