<script>
import { mapActions, mapState } from "vuex";
import MapBreadcrumbs from "../plugins/MapBreadcrumbs";

export default {
  status: "mapTaipei",
  created() {
    this.getAllTownUrgentsByCitys(["001", "002", "003"]);
    this.setTwoBreadCrumb(this.viewTag)
  },
  components:{
    MapBreadcrumbs
  },
  data() {
    return {
      Keelung: null,
      viewTag:{tag:'北北基', map:'mapTaipei'}
    };
  },
  methods: {
    ...mapActions("doorsStatusModule", [
      "getAllTownUrgentsByCitys",
      "selectUrgentCity",
    ]),
    ...mapActions("mapModule", [
      "showTownshipView",
      "setTwoBreadCrumb"
    ]),
    checkCityHaveUrgent(id) {
      return this.urgentCitys[id];
    },
    checkKeelungUrgnet() {
      let totalCount = 0
      let assignCount = 0
      Object.keys(this.urgentCitys).forEach((item) => {
        if(item == "city_200"||item == "city_201"||item == "city_202"||item == "city_203"||item == "city_204"||item == "city_205"||item == "city_207"){
          totalCount = totalCount + this.urgentCitys[item].totalCount
          assignCount = assignCount + this.urgentCitys[item].assignCount
        }
      });
      return totalCount > 0 ? this.urgentCitys['keelung'] = {totalCount,assignCount} : null;
    },
    checkTaipeiUrgnet() {
      let totalCount = 0
      let assignCount = 0
      Object.keys(this.urgentCitys).forEach((item) => {
        if(item == "city_100"||item == "city_103"||item == "city_104"||item == "city_105"
          ||item == "city_106"||item == "city_108"||item == "city_110"||item == "city_111"
          ||item == "city_112"||item == "city_114"||item == "city_115"||item == "city_116"){
            totalCount = totalCount + this.urgentCitys[item].totalCount
            assignCount = assignCount + this.urgentCitys[item].assignCount
          }
      });
      return totalCount > 0 ? this.urgentCitys['taipei'] = {totalCount,assignCount} : null;
    },
    checkNeedAssign(city_id) {
      let total = this.urgentCitys[city_id].totalCount
      let assign = this.urgentCitys[city_id].assignCount
      console.log("city_id " + city_id + " totalCount " +total +" assignCount ",assign)
      return assign == total
    },
  },
  computed: {
    ...mapState("doorsStatusModule", ["urgentCitys"]),
    ...mapState("websocketModule", ["haveNewData"]),
  },
  watch:{
    urgentCitys(){
      this.checkTaipeiUrgnet()
      this.checkKeelungUrgnet()
    },
    haveNewData() {
      this.getAllTownUrgentsByCitys(["001", "002", "003"]);
      this.setTwoBreadCrumb(this.viewTag)
    },
  }
};
</script>
