<style scope>
.v-main {
  padding: 80px 0px 0px !important;
}
.v-main__wrap {
  position: absolute;
  right: 0;
  top: 0;
  /* padding-top: 80px; */
}
.v-application--wrap {
  min-height: auto !important;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.text-red {
  color: red;
  position: relative;
}
.text-red:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: red;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.pageinfo {
  width: 80%;
  max-width: 900px;
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: -320px;
}
.theme--light.v-btn.btn_add {
  color: #fff;
}
</style>
<template>
  <v-container>
    <v-row class="mt-4">
      <v-col cols="4">
        <v-text-field
          rounded
          label=""
          placeholder="捲門序號"
          append-icon="mdi-magnify"
          xsmall
          filled
          background-color="#F3F3F3"
          color="#333333"
          dense
          height="42"
        ></v-text-field>
      </v-col>
      <v-col 
      cols="2"
      offset="6"
      >
        <div class="text-right">
          <v-btn
            rounded
            color="#B4B1A1"
            class="btn_add"
            @click="openCreateAssign()"
          >
            <v-icon left>
              mdi-plus-circle
            </v-icon>
            新增派單
          </v-btn>
        </div>
      </v-col>
      <v-col cols="12" class="text-center">
        <!-- <v-app id="inspire"> -->
        <v-simple-table>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-center">捲門序號</th>
                <th class="text-center" width="100">狀態</th>
                <th class="text-center">客戶名稱</th>
                <th class="text-center">委派車輛</th>
                <th class="text-center">委派時間</th>
                <th class="text-center">Actions</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in assignList" :key="item.number">
                <td>{{ item.sn }}</td>
                <td >
                    <Doorstatus :doorStatus="item.doorStatus" />
                </td>
                <td>{{ item.name }}</td>
                <td>{{ item.car_plate }}</td>
                <td>{{ setTime(item.createdAt) }}</td>
                <td>
                  <v-btn @click="openInfoList(item)">詳細內容</v-btn>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <!-- </v-app> -->
      </v-col>
      <div class="pageinfo">
        <v-container class="max-width">
          <v-pagination
            v-model="pagination.currentPage"
            class="my-4"
            :length="pagination.totalPage"
            @input="handlePageChange"
          ></v-pagination>
        </v-container>
      </div> 
    </v-row>
    <AssignInfo  :initEdit="true"/>
    <!-- <AssignCreate /> -->
            <component :is="showView"></component>
    <!-- <component :is="dailogComponent" ></component> -->
  </v-container>
</template>

<script>
// import { mapActions, mapState } from "vuex";
import { mapActions, mapState } from "vuex";
import AssignInfo from "./AssignInfo";
import AssignCreate from "./AssignCreate";
import Doorstatus from "./plugins/DoorStatus";
import moment from "moment";

export default {
  status: "AssignList",
  components: {
    AssignInfo,
    AssignCreate,
    Doorstatus
  },
  data() {
    return {
      dialog: false,
      currentPage: 1,
    };
  },
  created() {
    const currentPage  = this.pagination.currentPage;
    this.getAllAssignings( currentPage );
  },
  methods: {
    ...mapActions("doorsModule", ["getNeedAssignDoors"]),
    ...mapActions("assignModule", ["showCreateAssignDailog","getAllAssignings","showAssignInfoDailog","setAssignInfoPage"]),
    openInfoList(assign){
      // console.log("openInfoList",assign)
      this.setAssignInfoPage(assign)
      this.showAssignInfoDailog(true)
    },
    openCreateAssign(){
      let isShow = true
      let view = "AssignCreate"
      this.showCreateAssignDailog({ isShow, view });
    },
    handlePageChange(currentPage) {
    this.getAllAssignings(currentPage );
    },
    setTime(time_str){
      return moment(time_str).format("YYYY-MM-DD HH:mm:ss")
    }
  },
  computed: {
    ...mapState("assignModule", ["assignList","pagination","showView"]),
  }
};
</script>
